
.tileView {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    font-size: 2em;
    border: 2px solid #ddd;
    border-radius: 8px;

    box-sizing: border-box;
    cursor: pointer;

    transition: transform 0.3s ease-in-out;
}

.tileView:hover {
    transform: scale(1.1);
    transition: transform 0.05s ease-in-out;
}

.entities {
    display: flex;
    gap: 4px;

    font-size: 0.6em;
}
