
.container {
    width: 100%;
    padding: 12px;

    border: 1px solid #ccc;
    border-radius: 4px;
    background-color: #fafafa;

    color: #333;
    font-family: Arial, sans-serif;

    box-sizing: border-box;
}

.empty {
    padding: 16px;

    color: #777;
    font-style: italic;
    text-align: center;
}

.body {
    display: flex;
    gap: 12px;

    margin-top: 12px;
}

.tileWrapper {
    display: grid;

    width: 80px;
    height: 80px;

    flex-shrink: 0;
}

.info {
    display: flex;
    flex-direction: column;
    flex-grow: 1;

    gap: 4px;
    padding: 4px 0;
}

.terrainLine {
    display: flex;
    align-items: center;
    gap: 6px;
}

.terrainLine span {
    padding: 0 8px;

    border: 2px solid #999;
    border-radius: 4px;

    font-weight: 600;
}

.field {
    font-size: 0.95em;
}

.field span {
    font-weight: 600;
}

.emptyFeature {
    padding: 4px;

    color: #777;
    font-style: italic;
}

.entityList {
    display: flex;
    flex-direction: column;

    gap: 4px;
    margin-top: 8px;
}

.entityListItem {
    display: flex;
    align-items: center;

    gap: 6px;
}

.entityListItem:hover {
    background-color: #e0e0e0;
    cursor: pointer;
}

.entityListItem:hover .entityNames {
    color: #222;
}

.entityIcon {
    font-size: 1.2em;
    line-height: 1.5;
}

.entityNames {
    font-size: 0.95em;
}
